.adminPage {
  width: 1200px;
  height: auto;
  margin: 0 auto; }

.table {
  width: 1200px;
  height: auto;
  text-align: left;
  font-size: 10px; }
  .table a {
    display: inline-block; }
  .table tr {
    border: solid 1px #dce2eb;
    height: 20px;
    line-height: 20px;
    vertical-align: middle; }
    .table tr th {
      height: 50px;
      line-height: 50px;
      vertical-align: middle;
      padding-left: 20px;
      font-size: 12px;
      font-weight: normal;
      color: #afc2db;
      background-color: #f7f8fb; }
  .table td {
    height: 80px;
    line-height: 80px;
    vertical-align: middle;
    padding-left: 20px;
    font-size: 15px;
    color: #495057; }



