.noticeDetail {
  width: 1200px;
  text-align: left;
  font-size: 10px; }
  .noticeDetail tr {
    border: solid 1px #dce2eb;
    height: 20px;
    line-height: 20px;
    vertical-align: middle; }
    .noticeDetail tr th {
      width: 100px;
      height: 50px;
      line-height: 50px;
      vertical-align: middle;
      padding-left: 20px;
      font-size: 12px;
      font-weight: normal;
      color: #495057;
      background-color: #f7f8fb; }
  .noticeDetail .content {
    background-color: #ffffff; }
  .noticeDetail td {
    height: 80px;
    line-height: 80px;
    vertical-align: middle;
    padding: 60px;
    font-size: 15px;
    color: #495057; }

.buttonWrapper {
  margin-top: 20px;
  text-align: right; }
  .buttonWrapper button {
    width: 150px;
    height: 60px;
    line-height: 40px;
    vertical-align: middle;
    margin-right: 5px;
    font-size: 12px;
    font-weight: normal;
    text-align: center;
    color: #ffffff;
    background-color: #647dff;
    border: solid 1px #647dff; }



